import { useEffect, useMemo, useState } from 'react';
import { css } from '@emotion/react';

type HastNode = {
  type?: string;
  tagName?: string;
  value?: string;
  properties?: {
    id?: string;
    [key: string]: unknown;
  };
  children?: HastNode[];
};

type HeadingItem = {
  id: string;
  text: string;
  level: number;
};

type Props = {
  htmlAst: unknown;
  initialHeadings?: HeadingItem[];
};

const headingTagMap: Record<string, number> = {
  h2: 2,
  h3: 3,
  h4: 4,
};

function isHastNode(node: unknown): node is HastNode {
  return typeof node === 'object' && node !== null;
}

function getHeadingText(node: HastNode): string {
  if (typeof node.value === 'string') {
    return node.value;
  }

  if (!node.children || node.children.length === 0) {
    return '';
  }

  return node.children.map((child) => getHeadingText(child)).join('');
}

function collectHeadings(node: HastNode): HeadingItem[] {
  const headings: HeadingItem[] = [];

  const walk = (current: HastNode) => {
    if (current.tagName) {
      const level = headingTagMap[current.tagName];

      if (level && current.properties?.id) {
        const text = getHeadingText(current).trim();

        if (text) {
          headings.push({
            id: String(current.properties.id),
            text,
            level,
          });
        }
      }
    }

    current.children?.forEach((child) => {
      if (isHastNode(child)) {
        walk(child);
      }
    });
  };

  walk(node);

  return headings;
}

export function extractHeadings(htmlAst: unknown): HeadingItem[] {
  if (!isHastNode(htmlAst)) {
    return [];
  }

  return collectHeadings(htmlAst);
}

export default function TableOfContents({ htmlAst, initialHeadings }: Props) {
  const headings = useMemo(
    () => (initialHeadings ? initialHeadings : extractHeadings(htmlAst)),
    [htmlAst, initialHeadings],
  );

  const [activeId, setActiveId] = useState<string | null>(null);
  const [isVisible, setIsVisible] = useState(true);
  const [showToc, setShowToc] = useState(false);

  const parentMap = useMemo(() => {
    const map: Record<string, string> = {};
    let currentH2: string | null = null;
    let currentH3: string | null = null;

    headings.forEach((heading) => {
      if (heading.level === 2) {
        currentH2 = heading.id;
        currentH3 = null;
      } else if (heading.level === 3) {
        currentH3 = heading.id;
        if (currentH2) {
          map[heading.id] = currentH2;
        }
      } else if (heading.level === 4) {
        if (currentH3) {
          map[heading.id] = currentH3;
        } else if (currentH2) {
          map[heading.id] = currentH2;
        }
      }
    });

    return map;
  }, [headings]);

  const shouldShowH3 = (h3Id: string): boolean => {
    const parent = parentMap[h3Id];
    if (!parent) {
      return false;
    }

    if (parent === activeId) {
      return true;
    }

    if (h3Id === activeId) {
      return true;
    }

    if (activeId) {
      let currentId = activeId;
      while (currentId) {
        const currentParent = parentMap[currentId];
        if (currentParent === h3Id) {
          return true;
        }
        currentId = currentParent;
      }

      const activeParent = parentMap[activeId];
      if (activeParent === parent) {
        return true;
      }

      if (activeParent) {
        const activeGrandParent = parentMap[activeParent];
        if (activeGrandParent === parent) {
          return true;
        }
      }
    }

    return false;
  };

  const shouldShowH4 = (h4Id: string): boolean => {
    const parent = parentMap[h4Id];
    if (!parent) {
      return false;
    }

    if (parent === activeId) {
      return true;
    }

    if (h4Id === activeId) {
      return true;
    }

    if (activeId) {
      const activeParent = parentMap[activeId];
      if (activeParent === parent) {
        return true;
      }
    }

    return false;
  };

  const shouldShowHeading = (heading: HeadingItem) => {
    if (heading.level === 2) {
      return true;
    }

    if (heading.level === 3) {
      return shouldShowH3(heading.id);
    }

    if (heading.level === 4) {
      return shouldShowH4(heading.id);
    }

    return false;
  };

  useEffect(() => {
    if (typeof window === 'undefined' || typeof document === 'undefined') {
      return;
    }

    if (headings.length === 0) {
      return;
    }

    const SCROLL_THRESHOLD = 200;

    const updateActiveHeading = () => {
      const scrollPosition = window.scrollY + 120;

      // 스크롤 위치에 따라 TOC 표시/숨김
      setShowToc(window.scrollY > SCROLL_THRESHOLD);

      let currentActive: string | null = headings[0]?.id ?? null;

      for (const heading of headings) {
        const element = document.getElementById(heading.id);
        if (!element) {
          continue;
        }

        const elementTop = element.getBoundingClientRect().top + window.scrollY;
        if (elementTop <= scrollPosition) {
          currentActive = heading.id;
        } else {
          break;
        }
      }

      setActiveId(currentActive);
    };

    let ticking = false;
    const handleScroll = () => {
      if (ticking) {
        return;
      }

      ticking = true;
      window.requestAnimationFrame(() => {
        updateActiveHeading();
        ticking = false;
      });
    };

    const passiveOptions: AddEventListenerOptions = { passive: true };

    window.addEventListener('scroll', handleScroll, passiveOptions);
    window.addEventListener('resize', handleScroll);

    updateActiveHeading();

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleScroll);
    };
  }, [headings]);

  useEffect(() => {
    // TOC가 보이는 상태일 때만 스크롤 (페이지 로드 시 의도치 않은 스크롤 방지)
    if (!activeId || !showToc) {
      return;
    }

    const tocNav = document.querySelector('[aria-label="Table of contents"]');
    const activeLink = tocNav?.querySelector(`a[href="#${activeId}"]`);

    if (activeLink && tocNav) {
      activeLink.scrollIntoView({
        behavior: 'smooth',
        block: 'nearest',
      });
    }
  }, [activeId, showToc]);

  useEffect(() => {
    const readNextSection = document.querySelector('.read-next');
    if (!readNextSection) {
      return;
    }

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          setIsVisible(!entry.isIntersecting);
        });
      },
      {
        rootMargin: '-100px 0px 0px 0px',
      },
    );

    observer.observe(readNextSection);

    return () => {
      observer.disconnect();
    };
  }, []);

  if (headings.length === 0) {
    return null;
  }

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  return (
    <div css={[TocContainer, (!isVisible || !showToc) && TocContainerHidden]}>
      <nav css={TocWrapper} aria-label="Table of contents">
        <p css={TocTitle}>목차</p>
        <ul css={TocList}>
          {headings.map((heading) => {
            const isCollapsible = heading.level >= 3;
            const isVisible = shouldShowHeading(heading);

            return (
              <li
                key={heading.id}
                css={[
                  TocListItem,
                  depthStyle[heading.level as keyof typeof depthStyle],
                  heading.id === activeId && TocListItemActive,
                  isCollapsible && !isVisible && TocListItemCollapsedHidden,
                  isCollapsible && isVisible && TocListItemCollapsedVisible,
                ]}
              >
                <a
                  href={`#${heading.id}`}
                  aria-current={heading.id === activeId ? 'true' : undefined}
                  onClick={(e) => {
                    e.preventDefault();
                    setActiveId(heading.id);

                    window.history.pushState(null, '', `#${heading.id}`);

                    const element = document.getElementById(heading.id);
                    if (element) {
                      const headerOffset = 52;
                      const elementPosition = element.getBoundingClientRect().top;
                      const offsetPosition = elementPosition + window.pageYOffset - headerOffset;

                      window.scrollTo({
                        top: offsetPosition,
                        behavior: 'smooth',
                      });
                    }
                  }}
                >
                  {heading.text}
                </a>
              </li>
            );
          })}
        </ul>
      </nav>
      <div css={TocDivider} />
      <button type="button" css={ScrollToTopButton} onClick={scrollToTop}>
        ↑ Top
      </button>
    </div>
  );
}

const TocContainer = css`
  border: 1px solid var(--post-full-border);
  border-radius: 12px;
  background: var(--background-color);
  box-shadow: 0 6px 12px rgb(0 0 0 / 0.03);
  width: 100%;
  max-height: calc(100vh - 140px);
  overflow: hidden;
  opacity: 1;
  transform: translateX(0);
  transition:
    opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1),
    transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
`;

const TocContainerHidden = css`
  opacity: 0;
  transform: translateX(20px);
  pointer-events: none;
`;

const TocWrapper = css`
  padding: 20px 24px 0;
  word-break: keep-all;
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
`;

const TocTitle = css`
  margin-bottom: 8px;
  font-size: 1.4rem;
  font-weight: 600;
  color: var(--post-card-title);
`;

const TocList = css`
  list-style: none;
  margin: 0;
  padding: 0;
`;

const TocListItem = css`
  margin: 0;
  position: relative;

  :before {
    content: '';
    position: absolute;
    left: -16px;
    top: 0;
    bottom: 0;
    width: 2px;
    background: var(--anchor-color);
    opacity: 0;
    transition: opacity 0.1s ease-in-out;
  }

  a {
    color: var(--anchor-secondary-color);
    font-size: 1.3rem;
    line-height: 1.4;
    text-decoration: none;
    transition: color 0.1s ease-in-out;

    :hover {
      color: var(--anchor-color);
    }
  }
`;

const TocListItemActive = css`
  a {
    color: var(--anchor-color);
  }

  :before {
    opacity: 1;
  }
`;

const depthStyle = {
  2: css`
    padding-left: 0;
  `,
  3: css`
    padding-left: 16px;
  `,
  4: css`
    padding-left: 32px;
  `,
};

const TocListItemCollapsedHidden = css`
  max-height: 0 !important;
  opacity: 0 !important;
  visibility: hidden;
  overflow: hidden;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  transition:
    max-height 0.3s ease-in-out,
    opacity 0.3s ease-in-out,
    margin 0.3s ease-in-out,
    padding 0.3s ease-in-out;
`;

const TocListItemCollapsedVisible = css`
  max-height: 200px;
  opacity: 1;
  visibility: visible;
  transition:
    max-height 0.3s ease-in-out,
    opacity 0.3s ease-in-out,
    margin 0.3s ease-in-out,
    padding 0.3s ease-in-out;
`;

const TocDivider = css`
  margin: 16px 24px 0;
  border: none;
  border-top: 1px solid var(--post-full-border);
`;

const ScrollToTopButton = css`
  width: 100%;
  padding: 8px 0;
  background: transparent;
  border: none;
  border-radius: 0 0 11px 11px;
  color: var(--anchor-color);
  font-size: 1.3rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-align: center;

  &:hover {
    /* background: var(--post-full-border); */
    color: var(--anchor-hover-color);
    transform: translateY(-2px);
  }

  &:active {
    transform: translateY(0);
  }
`;
